import { mat4 } from "gl-matrix";
export class Object3D {
    constructor() {
        this.matrix = mat4.create();
        this.centroid = [0, 0, 0];
    }
    translate(vector) {
        mat4.translate(this.matrix, this.matrix, vector);
    }
    rotate(rad, axis) {
        mat4.rotate(this.matrix, this.matrix, rad, axis);
    }
    scale(v) {
        mat4.scale(this.matrix, this.matrix, v);
    }
    setColor(color) {
        this.color = color;
    }
}
