import { mat4 } from "gl-matrix";
export class Camera {
    constructor() {
        this.matrix = mat4.create();
        mat4.perspective(this.matrix, Math.PI / 2, 1, 0.1, 1000);
        mat4.translate(this.matrix, this.matrix, [0, 0, -5]);
        mat4.scale(this.matrix, this.matrix, [4, 4, 1]);
    }
}
